'use client'

import { useState, useEffect } from 'react'
import { MapContainer, TileLayer, Marker, Popup, useMap } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import L from 'leaflet'
import MapFilters from './ui/MapFilters'

// Fix default icon issue with Leaflet in React
delete (L.Icon.Default.prototype as any)._getIconUrl
L.Icon.Default.mergeOptions({
  iconRetinaUrl:
    'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-2x.png',
  iconUrl:
    'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon.png',
  shadowUrl:
    'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
})

// Create custom orange icon for clubs and events
const orangeIcon = new L.Icon({
  iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-orange.png',
  iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-orange-2x.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
})

// Create custom blue icon for friends
const blueIcon = new L.Icon({
  iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-blue.png',
  iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-blue-2x.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
})

interface Pin {
  id: string
  type: 'club' | 'event' | 'friend'
  position: [number, number]
  title: string
  description?: string
  popularity?: number
  ageCategory?: string
  musicGenre?: string
  ticketPrice?: number
}

export default function MapNoSSR() {
  const [events, setEvents] = useState<Pin[]>([])
  const [friends, setFriends] = useState<Pin[]>([])
  const [densityPoints, setDensityPoints] = useState<{ position: [number, number]; intensity: number }[]>([])

  const [distance, setDistance] = useState('')
  const [popularity, setPopularity] = useState('')
  const [ageCategory, setAgeCategory] = useState('')
  const [musicGenre, setMusicGenre] = useState('')
  const [ticketPrice, setTicketPrice] = useState('')
  const [showFriends, setShowFriends] = useState(false)

  // Fetch events from API with filters
  useEffect(() => {
    const params = new URLSearchParams()
    if (popularity) params.append('popularity', popularity)
    if (ageCategory) params.append('ageCategory', ageCategory)
    if (musicGenre) params.append('musicGenre', musicGenre)
    if (ticketPrice) params.append('ticketPrice', ticketPrice)

    fetch(`/api/events?${params.toString()}`)
      .then((res) => res.json())
      .then((data) => setEvents(data))
  }, [popularity, ageCategory, musicGenre, ticketPrice])

  // Fetch friends if showFriends is true
  useEffect(() => {
    if (showFriends) {
      fetch('/api/friends')
        .then((res) => res.json())
        .then((data) => setFriends(data))
    } else {
      setFriends([])
    }
  }, [showFriends])

  // Fetch heatmap density points
  useEffect(() => {
    fetch('/api/heatmap')
      .then((res) => res.json())
      .then((data) => setDensityPoints(data))
  }, [])

  // Combine events and friends for markers
  const pins = [...events, ...friends]

  return (
    <div className="w-full h-[600px] rounded-lg overflow-hidden shadow-lg">
      <MapFilters
        distance={distance}
        setDistance={setDistance}
        popularity={popularity}
        setPopularity={setPopularity}
        ageCategory={ageCategory}
        setAgeCategory={setAgeCategory}
        musicGenre={musicGenre}
        setMusicGenre={setMusicGenre}
        ticketPrice={ticketPrice}
        setTicketPrice={setTicketPrice}
        showFriends={showFriends}
        setShowFriends={setShowFriends}
      />
      <MapContainer
        center={[51.0, 4.04]}
        zoom={13}
        scrollWheelZoom={true}
        className="w-full h-[540px]"
      >
        <TileLayer
          attribution='&copy; <a href="https://osm.org/copyright">OpenStreetMap</a>'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {pins.map((pin) => (
          <Marker
            key={pin.id}
            position={pin.position}
            icon={pin.type === 'friend' ? blueIcon : orangeIcon}
          >
            <Popup>
              <strong>{pin.title}</strong>
              {pin.description && <p>{pin.description}</p>}
            </Popup>
          </Marker>
        ))}
        {/* TODO: Add heatmap overlay here */}
      </MapContainer>
    </div>
  )
}
