'use client'

import React from 'react'

interface MapFiltersProps {
  distance: string
  setDistance: (value: string) => void
  popularity: string
  setPopularity: (value: string) => void
  ageCategory: string
  setAgeCategory: (value: string) => void
  musicGenre: string
  setMusicGenre: (value: string) => void
  ticketPrice: string
  setTicketPrice: (value: string) => void
  showFriends: boolean
  setShowFriends: (value: boolean) => void
}

const MapFilters: React.FC<MapFiltersProps> = ({
  distance,
  setDistance,
  popularity,
  setPopularity,
  ageCategory,
  setAgeCategory,
  musicGenre,
  setMusicGenre,
  ticketPrice,
  setTicketPrice,
  showFriends,
  setShowFriends,
}) => {
  return (
    <div className="p-4 bg-white rounded-t-lg shadow-md flex flex-wrap gap-4">
      <select
        value={distance}
        onChange={(e) => setDistance(e.target.value)}
        className="border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
        aria-label="Filter by distance"
      >
        <option value="">Distance (km)</option>
        <option value="1">Within 1 km</option>
        <option value="5">Within 5 km</option>
        <option value="10">Within 10 km</option>
      </select>

      <select
        value={popularity}
        onChange={(e) => setPopularity(e.target.value)}
        className="border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
        aria-label="Filter by popularity"
      >
        <option value="">Popularity</option>
        <option value="50">50+</option>
        <option value="75">75+</option>
        <option value="90">90+</option>
      </select>

      <select
        value={ageCategory}
        onChange={(e) => setAgeCategory(e.target.value)}
        className="border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
        aria-label="Filter by age category"
      >
        <option value="">Age Category</option>
        <option value="18-25">18-25</option>
        <option value="26-35">26-35</option>
        <option value="36-45">36-45</option>
      </select>

      <select
        value={musicGenre}
        onChange={(e) => setMusicGenre(e.target.value)}
        className="border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
        aria-label="Filter by music genre"
      >
        <option value="">Music Genre</option>
        <option value="EDM">EDM</option>
        <option value="Hip-Hop">Hip-Hop</option>
        <option value="Rock">Rock</option>
      </select>

      <select
        value={ticketPrice}
        onChange={(e) => setTicketPrice(e.target.value)}
        className="border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#c94a1f]"
        aria-label="Filter by ticket price"
      >
        <option value="">Ticket Price</option>
        <option value="0">Free</option>
        <option value="20">Up to $20</option>
        <option value="50">Up to $50</option>
      </select>

      <label className="flex items-center space-x-2">
        <input
          type="checkbox"
          checked={showFriends}
          onChange={(e) => setShowFriends(e.target.checked)}
          className="form-checkbox h-5 w-5 text-[#c94a1f]"
        />
        <span>Show Friends</span>
      </label>
    </div>
  )
}

export default MapFilters
